\n          v-if=\"nominee.category !== 'ARTIST'\"\n        >\n          \"{{ nominee.title && nominee.title }}\"\n        </h2>\n      </div>\n      <media-links\n        :youtube=\"nominee.youtube\"\n        :spotify=\"nominee.spotify\"\n        :category=\"category\"\n      />\n    </div>\n  </div>\n</template>\n\n<script lang=\"ts\">\nimport { defineComponent, PropType } from \"vue\";\nimport { ArtistAwardNominee, RecordAwardNominee } from \"../types/award\";\nimport MediaLinks from \"./MediaLinks.vue\";\nimport NomineeImage from \"./NomineeImage.vue\";\n\nexport default defineComponent({\n  name: \"NomineeCard\",\n  components: {\n    MediaLinks,\n    NomineeImage,\n  },\n  props: {\n    nominee: {\n      type: Object as PropType<RecordAwardNominee | ArtistAwardNominee>,\n      required: true,\n    },\n    category: {\n      type: String,\n      required: true,\n    },\n  },\n  data() {\n    const isMobile = window.innerWidth <= 414;\n\n    return { isMobile };\n  },\n  inject: [\"handleYoutubeClick\"],\n});\n</script>\n\n<style scoped>\n.nominee-card-container {\n  display: flex;\n  flex-direction: column;\n  width: 200px;\n  margin-top: 25px;\n}\n.nominee-info {\n  text-align: center;\n}\n.won {\n  border-color: #e2bfd4;\n  color: #e2bfd4;\n}\n\nh2,\nh3 {\n  margin: 0;\n  font-weight: 300;\n  font-family: \"Roboto\";\n  color: #00938e;\n  font-size: 20px;\n  line-height: 20px;\n}\n\n.artist,\n.record {\n  margin-top: 25px;\n}\n\n.feat {\n  margin-top: 5px;\n  font-size: 17px;\n}\n\n.record.no-feat {\n  margin-top: 50px;\n}\n.star-wrapper {\n  text-align: center;\n}\nspan {\n  margin-bottom: 10px;\n  font-size: 20px;\n  color: #e2bfd4;\n}\n\n.hide {\n  visibility: hidden;\n}\n\n@media (max-width: 414px) {\n  .nominee-card-container {\n    flex-direction: row;\n    height: 80px;\n    width: auto;\n    margin-top: 8px;\n  }\n  .nominee-card-container:first-of-type {\n    margin-top: 16px;\n  }\n  .nominee-info {\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n    margin-left: 8px;\n    width: 100%;\n  }\n  .record-info {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    text-align: left;\n  }\n  .artist,\n  .record {\n    font-size: 18px;\n    line-height: 1;\n    margin-top: 8px;\n  }\n  .artist {\n    margin-top: 0;\n  }\n  .feat {\n    font-size: 16px;\n    line-height: 1;\n    margin-top: 0;\n  }\n  .record.no-feat {\n    margin-top: 8px;\n  }\n  .star-wrapper {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    margin-right: 8px;\n  }\n  span {\n    font-size: 16px;\n  }\n}\n\n@media (max-width: 375px) {\n  .nominee-card-container {\n    height: 72px;\n  }\n}\n</style>\n"],"sourceRoot":""}]);
